.spinn2 {
  display: inline-block;
  position: relative;
  left: 50%;
  width: 80px;
  height: 80px;
}
.spinn2 div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: rgb(146, 17, 17);
  animation: spinn2 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.spinn2 div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.spinn2 div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.spinn2 div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}
@keyframes spinn2 {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}
