.Spinner_spinn__1G4gp {
    display: inline-block;
    width: 80px;
    height: 80px;
    position: fixed; /* or absolute */
    top: 30%;
    left: 50%;
  }
  .Spinner_spinn__1G4gp div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid rgb(201, 41, 41);
    border-radius: 50%;
    -webkit-animation: Spinner_spinn__1G4gp 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: Spinner_spinn__1G4gp 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: rgb(187, 33, 33) transparent transparent transparent;
  }
  .Spinner_spinn__1G4gp div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
  }
  .Spinner_spinn__1G4gp div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
  }
  .Spinner_spinn__1G4gp div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
  }
  @-webkit-keyframes Spinner_spinn__1G4gp {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @keyframes Spinner_spinn__1G4gp {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
.Spinner2_spinn2__3XrEc {
