          animation-delay: -0.12s;
}
.Spinner2_spinn2__3XrEc div:nth-child(3) {
  left: 56px;
  -webkit-animation-delay: 0;
          animation-delay: 0;
}
@-webkit-keyframes Spinner2_spinn2__3XrEc {
  0% {
    top: 8px;
    height: 64px;
  }
